.sidebar {
  width: 250px;
  background-color: #f8f9fa; /* Light, clean background */
  color: #2c3e50; /* Darker font color for contrast */
  padding: 20px;
  height: 100vh;
  overflow-y: auto; /* Smooth scrolling if content is long */
  box-shadow: 2px 0 5px rgba(0, 0, 0, 0.1); /* Subtle shadow for depth */
  border-right: 1px solid #e0e0e0; /* Light border for separation */
}

.sidebar-header {
  text-align: center;
  margin-bottom: 20px;
}

.sidebar h2 {
  font-size: 1.3rem;
  font-weight: bold;
  color: #333;
}

.sidebar ul {
  list-style-type: none;
  padding: 0;
}

.sidebar ul li {
  margin-bottom: 15px;
}

.sidebar ul li a {
  display: flex;
  align-items: center;
  color: #2c3e50;
  text-decoration: none;
  font-size: 1rem;
  padding: 10px;
  border-radius: 8px;
  transition: background-color 0.3s ease, color 0.3s ease;
}

.sidebar ul li a:hover {
  background-color: #e9ecef; 
  color: #007bff; 
}

.link-icon {
  margin-right: 10px;
  font-size: 1rem;
  color: #6c757d; /* Soft color for icons */
}

/* Style for the active link */
.active-link {
  background-color: #3a99ff;
  color: #fff; 
}
