.checkbox-example {
    padding: 20px;
    background-color: #f4f4f4;
    border-radius: 8px;
    max-width: 500px;
    margin: 20px auto;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
}

h2 {
    color: #333;
    text-align: center;
    margin-bottom: 10px;
}

.checkbox-group {
    display: flex;
    flex-direction: row;
    gap: 10px;
    margin-top: 10px;
}

.checkbox-item {
    display: flex;
    align-items: center;
    gap: 10px;
}

input[type="checkbox"] {
    width: 16px;
    height: 16px;
}

.selected-summary {
    margin-top: 20px;
    padding: 10px;
    background-color: #fff;
    border: 1px solid #ddd;
    border-radius: 4px;
}

.selected-summary ul {
    padding-left: 20px;
    list-style-type: disc;
}

.selected-summary p {
    color: #666;
    font-style: italic;
}


.checkbox-heading{
    font-size: 2.3125em;
  text-align: justify;
}